.mt-2 {
  margin-top: 8px !important;
}
.mr-2 {
  margin-right: 8px !important;
}
.mr-3 {
  margin-right: 16px !important;
}
.ml-3 {
  margin-left: 16px !important;
}
.mb-3 {
  margin-bottom: 16px !important;
}
.padding-left {
  padding-left: 8px !important;
  box-sizing: border-box;
}
.padding-right {
  padding-right: 8px !important;
  box-sizing: border-box;
}
.row {
  display: flex;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji;
  flex-wrap: wrap;
  flex: 1;
  box-sizing: border-box;
  max-width: 920px;
}
.col-12 {
  width: 100%;
}
@media screen and (min-width: 768px) {
  .col-md-6 {
    width: 50%;
  }
}
@media screen and (min-width: 1012px) {
  .col-lg-6 {
    width: 50%;
  }
}
.main-content {
  background-color: white;
  border: 1px solid #e1e4e8;
  padding: 16px;
  border-radius: 6px;
  color: #586069;
}
.main-content p {
  margin: 0;
  padding: 0;
  font-size: 12px;
}
.main-content a {
  text-decoration: none;
  font-weight: 600;
  color: #0366d6;
}
.main-content .repo-header:hover {
  text-decoration: underline;
}
.lang-color {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.svg-icon {
  fill: currentColor;
  display: inline-block;
  overflow: visible !important;
  vertical-align: text-bottom;
}
.github-icon {
  text-decoration: none !important;
  color: #586069 !important;
  word-wrap: break-word;
  line-height: 1.5;
}
.github-icon:hover {
  color: #0366d6 !important;
}
.svg {
  display: inline;
  vertical-align: text-bottom;
}
.loading {
  background-color: #cfe2ff;
  text-align: center;
  color: #6a7491;
}
.error {
  background-color: #f8d7da;
  text-align: center;
  color: #672012;
}
